import { React, useState, useEffect } from "react";

export const KakaoShare = (route, title) => {
  // url id    route  
  if (window.Kakao) {
    const kakao = window.Kakao;

    kakao.Link.sendDefault({
      objectType: "feed", //     type  feed  ->    
      content: {
        title: title, //   title
        description: "  ,   !", //   title
        imageUrl:
          "https://cdn.banggooso.com/assets/images/2022-meme-awards/share-kakao.png",
        link: {
          mobileWebUrl: route, //   route(uri )
          webUrl: route,
        },
      },
      buttons: [
        {
          title: "  ",
          link: {
            mobileWebUrl: route,
            webUrl: route,
          },
        },
      ],
    });
  }
};

export const shareTwitter = (url) => {
  const text = "2022 ,     !";
  window.open("https://twitter.com/intent/tweet?text=" + text + "&url=" + url);
};
